<template>
  <div>
    <div class="fill">
      <div class="bg"></div>
      <div class="bg bg2"></div>
      <div class="bg bg3"></div>
    </div>
    <v-container>
      <v-card>
        <v-container>
          <h1>My Projects</h1>
          <p>This page contains most of my completed projects. But much like any tinkerer I have many more
            (experimental) projects always going on! Maybe also check out my <a
              href="https://github.com/kvoeten">github</a> if you're curious. (Not all my projects are public yet though
            hehe..)</p>
        </v-container>
      </v-card>
      <v-row class="mb-3" style="padding-top: 30px;">
        <v-col v-for="card in cards" :key="card.project" :lg="4" :md="6" :sm="6" :xs="6">
          <ProjectCard :project="card.project" />
        </v-col>
      </v-row>
      <v-card>
        <v-container>
          <v-pagination v-model="page" :length="length" :page="page" color="#e4b040" @input="setPage"></v-pagination>
        </v-container>
      </v-card>
    </v-container>
  </div>
</template>

<script>
import projects from '@/services/projects'
import ProjectCard from '@/components/projects/ProjectCard'

export default {
  components: {
    ProjectCard
  },
  computed: {
  },
  data: () => ({
    page: 1,
    length: 1,
    cards: []
  }),
  methods: {
    async setPage(page) {
      this.getCards(page)
    },
    async getCards(page) {
      this.cards = []
      const entries = projects.getPage(page);
      for (let i = 0; i < entries.length; ++i) {
        const entry = entries[i];
        this.cards.push({
          project: entry.name
        })
      }
    }
  },
  async mounted() {
    this.page = 1;
    this.length = projects.getNumPages();
    this.setPage(this.page);
  }
}
</script>

<style scoped>
.fill {
  width: 100%;
  top: 0;
  right: 0;
  left: 0;
}

.bg {
  animation: slide 60s ease-in-out infinite alternate;
  background-image: linear-gradient(35deg, #505050 50%, #303030 50%);
  bottom: 0;
  left: -50%;
  opacity: .5;
  position: fixed;
  right: -50%;
  top: 0;
  z-index: 0;
}

.bg2 {
  animation-direction: alternate-reverse;
  animation-duration: 35s;
}

.bg3 {
  animation-duration: 35s;
}

@keyframes slide {
  0% {
    transform: translateX(25%);
  }

  100% {
    transform: translateX(-25%);
  }
}
</style>
