<template>
    <v-card shaped class="mx-auto" max-height="800px">
        <v-img class="white--text align-end" height="250px" :src="card.image">
        </v-img>

        <v-card-title
            style="padding-top: 16px; padding-left: 16px; padding-bottom: 0px; white-space: nowrap; overflow: hidden;text-overflow: ellipsis;">
            <h2>{{ card.title }}</h2>
        </v-card-title>
        <v-card-title
            style="padding-top: 5px; padding-left: 16px; white-space: nowrap; overflow: hidden;text-overflow: ellipsis;">
            <h1>{{ card.role }}</h1>
        </v-card-title>

        <v-card-subtitle class="text-left">{{ card.date }} - {{ card.company }}</v-card-subtitle>
        <v-card-text class="text-left" style="overflow: hidden;text-overflow: ellipsis;" v-html="card.description">
        </v-card-text>

        <v-card-actions v-if="button">
            <v-btn  v-if="!localbutton" color="orange" text :href="card.buttonlink"  target="_blank">
                READ MORE
            </v-btn>
            <v-btn  v-if="localbutton" color="orange" text :to="card.buttonlink">
                READ MORE
            </v-btn>
        </v-card-actions>
    </v-card>
</template>

<script>
import projects from '@/services/projects'

export default {
    props: {
        project: String,
    },
    components: {

    },
    computed: {
    },
    data: () => ({
        button: false,
        localbutton: false,
        card: {
            "name" : "",
            "author": "Kaz Voeten",
            "date": "2021-01-31 08:14:09",
            "title": "",
            "company": "",
            "role": "",
            "roledescription": "",
            "description": "",
            "buttontext": "",
            "buttonlink": "",
            "image": "/images/landing/landing-backdrop-new.png"
        }
    }),
    methods: {
    },
    async mounted() {
        this.card = projects.getByName(this.project)
        this.card.date = projects.getDateText(this.card.date)
        this.button = this.card.buttontext !== ""
        this.localbutton = !this.card.buttonlink.includes("https://")
    }
}
</script>

<style scoped>
h1 {
    font-size: 30px;
}
h2 {
    font-size: 20px;
}
</style>